import { h, Component } from 'preact';
import { Link } from 'preact-router/match';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import linkState from 'linkstate';
import { route } from 'preact-router';
import { Text, Localizer } from 'preact-i18n';

import mu from '../../lib/musher-util';

const SUB_MENU_MARGIN_TOP = mu.subMenuMarginTop();

const initialState = {
};

@observer
class SubNavigation extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
    }

    back = () => {
        const { appState } = this.props.stores;
        const { previousPath = '/' } = appState;
        route(previousPath);
    }

    render() {
        const { title, backtext } = this.props;
        const { appState } = this.props.stores;
        const { darkmode } = appState;
		return (
            <div class='row fixed-top border-bottom' style={`margin-top: ${SUB_MENU_MARGIN_TOP}; background-color: ${darkmode ? '#191d21' : '#f8f9fa'};`}>
                <div class={`col text-center subtopmenu position-relative`}>
                    <div class={`text-left subtopmenu position-absolute`} style='top: 0px; left: 20px; line-height: 1.8em;'>
                        <button
                            class='btn btn-link text-secondary p-0'
                            onClick={this.back}
                        >
                            <nobr>
                                <i class='fas fa-chevron-left font-weight-bold' /> {backtext ? <>
                                    {backtext}
                                </> : <>
                                    <Text id='home.home'>Home</Text>
                                </>}
                            </nobr>
                        </button>
                    </div>
                    <nobr>
                        <strong>
                            {title}
                        </strong>
                    </nobr>
                </div>
            </div>
		);
	}
}

export default SubNavigation;
