import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import { route } from 'preact-router';
import { Text, Localizer } from 'preact-i18n';
import Markdown from 'preact-markdown';

import PubSub, { topics } from '../../lib/pubsub';
import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(true);
const MARGIN_TOP_BACK = mu.marginTopBack(false);
const MARGIN_BOTTOM = mu.marginBottom();

import DogSimplePublic from '../../components/dog/simplePublic';
import PublicTeam from '../../components/team/public';
import TeamConnectConfirm from '../../components/team/connect-confirm';
import TeamLine from '../../components/team/publicLine';
import SubNavigation from '../../components/header/subnavigation';
import UserInvite from '../../components/user/invite';

const initialState = {
	requestSent: {},
    submenu: 'notfollowing',
};

@observer
class TeamsDetail extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
        this.updateTimer = {};
    }

    async loadAll(props = this.props) {
        await this.loadPublicTeams(props);
    }

    async loadPublicTeams(props = this.props) {
		const { teamStore } = props.stores;
        const { teamid } = props;
        await teamStore.loadPublic({});
    }

    back = () => {
        const { history } = this.props.stores;
        history.goBack();
    }

    chooseSubmenu = (e) => {
		const submenu = e.target.parentElement.dataset.menu || e.target.dataset.menu;
		this.setState({ submenu });
	}

	componentDidMount() {
        this.loadAll();
    }

    componentWillReceiveProps(nextProps) {
        // console.log('componentWillReceiveProps', nextProps);
        if (nextProps.dog !== this.props.dog) {
            this.loadAll(nextProps);
        }
    }

	render() {
        const { submenu } = this.state;
		const { teamStore, userStore, storyStore } = this.props.stores;
        const { user } = userStore;
        const { language = 'en' } = user;
		const { publicTeamsToFollow } = teamStore;

        const waitingForApproval = publicTeamsToFollow.filter(e => e.followRequests && e.followRequests.findIndex(f => f.id === user.id) > -1);
        const following = publicTeamsToFollow.filter(e => e.followers && e.followers.indexOf(user.id) > -1);
        const notFollowing = publicTeamsToFollow.filter(e => {
            const isYourTeam = e.members && e.members.findIndex(t => t.id === user.id) > -1;
            if (isYourTeam) {
                return false;
            }
            if (waitingForApproval.findIndex(t => t.id === e.id) > -1) {
                return false;
            }
            if (following.findIndex(t => t.id === e.id) > -1) {
                return false;
            }
            return true;            
        });            
		return (<>
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
                <Localizer>
                    <SubNavigation stores={this.props.stores}
                        title={<Text id='home.find-teams'>Find teams to follow</Text>}
                        backtext={<Text id='home.back'>Back</Text>}
                    />
                </Localizer>
    
                <div class='row mt-4'>
                    <div class={`col-12`}>
                        <TeamConnectConfirm stores={this.props.stores} />
                    </div>
                </div>

                <div class='row'>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3 my-3'>
						<div class='row'>
							<div class='col px-1 pt-2'>
                                <button
                                    class={`btn btn-block ${submenu === 'notfollowing' ? 'btn-primary' : 'btn-outline-primary'} text-nowrap`}
                                    style='text-overflow: ellipsis; overflow: hidden;'
                                    data-menu='notfollowing'
                                    onClick={this.chooseSubmenu}
                                ><i class='fas fa-tachometer-alt' /> <Text id='teams.notfollowing'>Not following</Text></button>
                            </div>
							<div class='col px-1 pt-2'>
                                <button
                                    class={`btn btn-block ${submenu === 'waiting' ? 'btn-primary' : 'btn-outline-primary'} text-nowrap`}
                                    style='text-overflow: ellipsis; overflow: hidden;'
                                    data-menu='waiting'
                                    onClick={this.chooseSubmenu}
                                ><i class='fas fa-chalkboard-teacher' /> <Text id='teams.waiting-approval'>Waiting for approval</Text></button>
                            </div>
							<div class='col px-1 pt-2'>
                                <button
                                    class={`btn btn-block ${submenu === 'following' ? 'btn-primary' : 'btn-outline-primary'} text-nowrap`}
                                    style='text-overflow: ellipsis; overflow: hidden;'
                                    data-menu='following'
                                    onClick={this.chooseSubmenu}
                                ><i class='fas fa-chalkboard-teacher' /> <Text id='teams.following'>Following</Text></button>
                            </div>
                        </div>
                    </div>
                </div>

                <div class='row mt-4'>
                    <div class={`col-12 col-sm-8 col-lg-6 offset-sm-2 offset-lg-3 mb-0 position-relative`}>
                        <div class='row'>
                            {submenu === 'notfollowing' && <>
                                {(!util.isArray(notFollowing) || notFollowing.length === 0) && <>
                                    <div class='col-12 text-center text-muted mb-5'>
                                        <div class='display-1'>
                                            <i class='fas fa-search' />
                                        </div>
                                        <h3 class='font-weight-lighter'>
                                            <Text id='teams.no-more-teams-to-follow'>No more teams to follow.</Text>
                                        </h3>
                                    </div>
                                    <UserInvite stores={this.props.stores} forceShow={true} />
                                </>}
                                {util.isArray(notFollowing) && notFollowing.map(team => <TeamLine stores={this.props.stores} team={team} />)}
                            </>}
                            {submenu === 'waiting' && <>
                                {(!util.isArray(waitingForApproval) || waitingForApproval.length === 0) && <>
                                    <div class='col-12 text-center text-muted mb-5'>
                                        <div class='display-1'>
                                            <i class='fas fa-check' />
                                        </div>
                                        <h3 class='font-weight-lighter'>
                                            <Text id='teams.no-pending-follow-requests'>No pending follow requests.</Text>
                                        </h3>
                                    </div>
                                </>}
                                <UserInvite stores={this.props.stores} forceShow={true} />
                                {util.isArray(waitingForApproval) && waitingForApproval.map(team => <TeamLine stores={this.props.stores} team={team} />)}
                            </>}
                            {submenu === 'following' && <>
                                {(!util.isArray(following) || following.length === 0) && <>
                                    <div class='col-12 text-center text-muted mb-5'>
                                        <div class='display-1'>
                                            <i class='fas fa-check' />
                                        </div>
                                        <h3 class='font-weight-lighter'>
                                            <Text id='teams.not-following-any-teams'>You are not following any teams.</Text>
                                        </h3>
                                    </div>
                                </>}
                                <UserInvite stores={this.props.stores} forceShow={true} />
                                {util.isArray(following) && following.map(team => <TeamLine stores={this.props.stores} team={team} />)}
                            </>}
                        </div>
                    </div>
                </div>

                <div class='row mt-4'>
                    <div class='col-12 offset-0 col-sm-8 offset-sm-2 col-lg-6 offset-lg-3'>
                        <div class='text-center text-muted'>
                            <div class='display-1'>
                                <i class='fas fa-ellipsis-h' />
                            </div>
                            <div style='line-height: 0.9em;'>
                                <small>
                                    <Text id='teams.something-missing'>Er det noe du savner p친 denne siden? Send oss en melding p친 Facebook.</Text>
                                </small>
                            </div>
                        </div>
                    </div>
                </div>

            </div>


		</>);
	}
}

export default TeamsDetail;
